<template>

<div id="calendar-container">

  <div id="calendar-background"></div>

  <FullCalendar ref="fullCalendar" :options="calendarOptions" />

</div>

</template>



<script>

import FullCalendar from "@fullcalendar/vue";
import timeGridPlugin from "@fullcalendar/timegrid";
import googleCalendarPlugin from "@fullcalendar/google-calendar";
export default { 
  name: "Calendar",
  components: {
    FullCalendar
  },
  watch: {
    "eventTimes" () {
      this.$refs.fullCalendar.getApi().setOption("slotMinTime", this.eventTimes[0]);
      let lastTime = this.eventTimes[this.eventTimes.length - 1];
      let index = lastTime.indexOf(":");
      let hour = lastTime.substring(0, index);
      let meridiem = lastTime.substring(lastTime.length - 2);
      let lastTime24;
      if (meridiem == "PM" && hour <= 12) {
        hour = parseInt(hour) + 12;
        hour = hour.toString();
        let lastTimeEnd = lastTime.substring(index);
        lastTime24 = `${hour}${lastTimeEnd}`;
      }
      this.$refs.fullCalendar.getApi().setOption("slotMaxTime", lastTime24);
    },
  },
  data() {
    return {
      calendarOptions: {
        plugins: [timeGridPlugin, googleCalendarPlugin],
        initialView: "timeGridWeek",
        height: "100%",
        allDaySlot: false,
        slotDuration: "01:00:00",
        expandRows: true,
        dayHeaderFormat: {
          weekday: "short"
        },
        eventTimeFormat: {
          hour: "numeric",
          minute: "2-digit",
          omitZeroMinute: true,
          meridiem: "short"
        },
        googleCalendarApiKey: "AIzaSyCD0WdtseGrjs-ufw30fFM5FnytAZ4u7GE",
        eventSources: [
          {
            // eric
            googleCalendarId: "1r7qnb1955jpseqreu7p138apo@group.calendar.google.com",
            color: "rgba(125, 161, 112, 0.9)",
            className: "acci"
          },
          {
            // molly
            googleCalendarId: "55nble53bqgjqtgg1hnk5f78ig@group.calendar.google.com",
            color: "rgba(146, 117, 209, 0.9)",
            className: "bearlymolly"
          },
          {
            // floppy
            googleCalendarId: "791gk3erfqq071usft3a88g2ak@group.calendar.google.com",
            color: "rgba(32, 81, 150, 0.9)",
            className: "floppymcdingdong"
          },
          {
            // geeky
            googleCalendarId: "0e4t66ol75m4pn7koib0768kmg@group.calendar.google.com",
            color: "rgba(59, 219, 211, 0.9)",
            textColor: "#6b2512",
            className: "geekyreader"
          },
          {
            // jaki
            googleCalendarId: "53os351dor02hhs1cnlrrtluj8@group.calendar.google.com",
            color: "rgba(130, 14, 20, 0.9)",
            className: "jakisaur"
          },
          {
            // dave
            googleCalendarId: "4bbk1m1rha7c54dc948jrqjp3o@group.calendar.google.com",
            color: "rgba(21, 99, 39, 0.9)",
            className: "megadave"
          },
          {
            // mompou
            googleCalendarId: "29kktrel09t6hh44lujbmrf72o@group.calendar.google.com",
            id: "mompou",
            color: "rgba(255, 84, 112, 0.9)",
            className: "mompou"
          },
          {
            // team
            googleCalendarId: "7o425p9hf6hktj3kogb9v8ll5s@group.calendar.google.com",
            color: "rgba(255, 255, 255, 0.9)",
            textColor: "#6b2512",
            className: "team"
          },
        ],
        nowIndicator: true,
        eventClick: function(info) {
          info.jsEvent.preventDefault();
          let text = info.el.text.toLowerCase();
          let index = text.search(/[a-z]/i);
          let user = text.substring(index);
          window.open(`https://www.twitch.tv/${user}`);
        },
        eventDidMount: (arg) => {
          let timeText = arg.timeText;
          let pushTime = (type) => {
            let index = type.search(/[a-z]/i);
            let time = `${type.slice(0, index)}:00 ${type.slice(index).toUpperCase()}`;
            if (!this.eventTimes.includes(time)) {
              this.eventTimes.push(time);
            }
          }
          let startTime = timeText.substr(0, timeText.indexOf(" "));
          pushTime(startTime);
          let endTime = timeText.substr(timeText.lastIndexOf(" ") + 1);
          pushTime(endTime);
          this.eventTimes.sort(function(a, b) {
            return Date.parse(`2020/01/01 ${a}`) - Date.parse(`2020/01/01 ${b}`);
          });
        }
      },
      eventTimes: []
    }
  }
}

</script>



<style lang="scss">


#calendar-container {
  height: 100%;
  width: 100%;
  position: relative;
  background-color: white;
  background: linear-gradient(rgba(245, 178, 91, 0.2), rgba(201, 79, 79, 0.5));
  padding: 50px;
  z-index: 0;
}

  #calendar-background {
    position: absolute;
    top: 0;
    right: 0;
