  }
  .fc-v-event .fc-event-title-container {
    width: 100%;
  }
    .fc-v-event .fc-event-title {
      width: 100%;
      overflow-wrap: break-word;
      text-align: center;
    }

.acci {
  background-image: url("../assets/hiveling.png");
}
.bearlymolly {
  background-image: url("../assets/moose.png");
}
.floppymcdingdong {
